.birthdayCard {
    position: relative;
    width: 300px;
    height: 450px;
    cursor: pointer;
    transform-style: preserve-3d;
    transform: perspective(2500px);
    transition: 1s;
}
.birthdayCard:hover {
    transform: perspective(2500px) rotate(5deg);
    box-shadow: inset 100px 20px 100px rgba(0, 0, 0, 0.2),
        0 10px 100px rgba(0, 0, 0, 0.5);
}

.birthdayCard:hover .cardFront {
    transform: rotateY(-160deg);
}

.birthdayCard:hover .happy {
    visibility: hidden;
}

.cardFront {
    position: relative;
    background-color: #fff;
    width: 300px;
        height: 450px;
    overflow: hidden;
    transform-origin: left;
    box-shadow: inset 100px 20px 100px rgba(0, 0, 0, 0.2),
        30px 0 50px rgba(0, 0, 0, 0.4);
    transition: 0.6s;
}

.happy {
    font-family: Tahoma, sans-serif;
    text-align: center;
    margin: 30px;
    background-image: linear-gradient(120deg, #f6d365 0%, #fda085 100%);
    transition: 0.1s;
    backface-visibility: visible;
}

.balloons {
    position: absolute;
}

.balloonOne,
.balloonTwo,
.balloonThree,
.balloonFour {
    position: absolute;
    width: 120px;
    height: 120px;
    border-radius: 50%;
}

.balloonOne {
    background-color: rgb(239, 71, 111, 0.7);
    left: -12px;
    top: 80px;
}

.balloonTwo {
    background-color: rgb(6, 214, 160, 0.7);
    left: 60px;
    top: 50px;
}

.balloonThree {
    background-color: rgb(255, 209, 102, 0.7);
    left: 130px;
    top: 80px;
}

.balloonFour {
    background-color: rgb(17, 138, 178, 0.7);
    left: 190px;
    top: 50px;
}
.name:before
.balloonOne:before,
.balloonTwo:before,
.balloonThree:before,
.balloonFour:before {
    content: "";
    position: absolute;
    width: 1px;
    height: 155px;
    background-color: #ffd166;
    top: 95px;
    left: 43px;
}
.name:after
.balloonOne:after,
.balloonTwo:after,
.balloonThree:after,
.balloonFour:after {
    content: "";
    position: absolute;
    border-right: 7px solid transparent;
    border-left: 7px solid transparent;
    top: 94px;
    left: 37px;
}

.balloonOne:after .name:after{
    border-bottom: 10px solid #ef476f;
}

.balloonTwo:after {
    border-bottom: 10px solid #06d6a0;
}

.balloonThree:after {
    border-bottom: 10px solid #ffd166;
}

.balloonFour:after {
    border-bottom: 10px solid #118ab2;
}

.cardInside  {
    position: absolute;
    background-color: #fff;
width: 300px;
    height: 450px;
    z-index: -1;
    left: 0;
    top: 0;
    box-shadow: inset 100px 20px 100px rgba(0, 0, 0, 0.2);
}

p {
    font-family: "Brush Script MT", cursive;
        font-size: 20px;
    margin: 35px;
    color: #333;
}

.name {
    position: absolute;
    width: 150px;
    left: 100px;
    top: 300px;
    color: #333;
}
.gift {
    position: absolute;
    width: 150px;
    height: 50px;
    left: 120px;
    top: 380px;
    color: #333333;
    border-color: transparent;
    background-color: transparent;
    font-family: "Brush Script MT", cursive;
    font-size: 20px;
}
.back {
    font-family: Tahoma, sans-serif;
        font-size: 20px;
    color: #333;
    text-align: center;
  
    outline-color: #333;
    outline-style: dotted;
padding-bottom: 15px}